/***********
1. Fonts
***********/

/*********************************
2. Body and some general stuff
*********************************/

*
{
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
body
{
	font-family: 'Josefin Sans', sans-serif;
	font-size: 14px;
	font-weight: 400;
	background: #FFFFFF;
	color: #a5a5a5;
}
div
{
	display: block;
	position: relative;
 box-sizing: border-box;
}
ul
{
	list-style: none;
	margin-bottom: 0px;
}
p
{
	font-family: 'Josefin Sans', sans-serif;
	font-size: 16px;
	line-height: 1.875;
	font-weight: 400;
	color: #6d6d6d;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
p a
{
	display: inline;
	position: relative;
	color: inherit;
	border-bottom: solid 1px #ffa07f;
	transition: all 200ms ease;
}
p:last-of-type
{
	margin-bottom: 0;
}
a
{
	transition: all 200ms ease;
}
a, a:hover, a:visited, a:active, a:link
{
	text-decoration: none;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
p a:active
{
	position: relative;
	color: #FF6347;
}
p a:hover
{
	color: #FFFFFF;
	background: #ffa07f;
}
p a:hover::after
{
	opacity: 0.2;
}
::selection
{
	background: rgba(42,194,117,0.9);
	color: #ffffff;
}
p::selection
{
	
}
h1{font-size: 48px;}
h2{font-size: 36px;}
h3{font-size: 24px;}
h4{font-size: 18px;}
h5{font-size: 14px;}
h1, h2, h3, h4, h5, h6
{
	font-family: 'Josefin Sans', sans-serif;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
	line-height: 1.2;
	color: #2a2a2a;
	font-weight: 700;
}
h1::selection, 
h2::selection, 
h3::selection, 
h4::selection, 
h5::selection, 
h6::selection
{
	
}
img
{
	max-width: 100%;
}
button:active
{
	outline: none;
}
.form-control
{
	color: #db5246;
}
section
{
	display: block;
	position: relative;
	box-sizing: border-box;
}
.clear
{
	clear: both;
}
.clearfix::before, .clearfix::after
{
	content: "";
	display: table;
}
.clearfix::after
{
	clear: both;
}
.clearfix
{
	zoom: 1;
}
.float_left
{
	float: left;
}
.float_right
{
	float: right;
}
.trans_200
{
	transition: all 200ms ease;
}
.trans_300
{
	transition: all 300ms ease;
}
.trans_400
{
	transition: all 400ms ease;
}
.trans_500
{
	transition: all 500ms ease;
}
.fill_height
{
	height: 100%;
}
.super_container
{
	width: 100%;
	overflow: hidden;
}
.prlx_parent
{
	overflow: hidden;
}
.prlx
{
	height: 130% !important;
}
.parallax-window
{
    min-height: 400px;
    background: transparent;
}
.parallax_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.background_image
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}
.nopadding
{
	padding: 0px !important;
}
.owl-carousel,
.owl-carousel .owl-stage-outer,
.owl-carousel .owl-stage,
.owl-carousel .owl-item
{
	height: 100%;
}
.slide
{
	height: 100%;
}
.button
{
	position: relative;
	overflow: hidden;
	width: 200px;
    height: 56px;
    border-radius: 22px;
    border: none;
    outline: none;
    cursor: pointer;
    text-align: center;
}
.button::after
{
	display: block;
	position: absolute;
	top: 0;
	left: -100%;
	width: 200%;
	height: 100%;
	background-color: #2ac275;
	background-image: linear-gradient(to right, #2ac275, #cbdc0b, #2ac275);
	background: #2ac275;
	background: linear-gradient(to right, #2ac275, #cbdc0b, #2ac275);
	content: '';
	z-index: 0;
	transition: all 400ms ease;
}
.button:hover::after
{
	left: 0;
}
.button a
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    text-transform: uppercase;
    z-index: 1;
    line-height: 56px;
}
.button span
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    line-height: 56px;
    text-transform: uppercase;
	z-index: 1;
}
.section_title h1
{
	font-size: 60px;
	font-weight: 400;
}

/*********************************
3. Header
*********************************/

.header
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	z-index: 100;
}
/*Header gradient bottom border*/
.header::after
{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	content: '';
	background-color: #2ac275;
	background-image: linear-gradient(to right, #eeff2d, #2ac275);
	background: #2ac275;
	background: linear-gradient(to right, #eeff2d, #2ac275);
}
.header_content
{
	width: calc(100% + 30px);
	left: -20px;
	height: 134px;
	transition: all 400ms ease;
}
.header.scrolled .header_content
{
	height: 75px;
}
.logo
{
	position: absolute;
	left: 0;
	top: 46%;
	transform: translateY(-50%);
}
.logo_bars
{
	width: 41px;
	height: 30px;
}
.logo_bars > span
{
	width: 6px;
}
.logo_bars > span:first-of-type,
.logo_bars > span:last-of-type
{
	height: 17px;
	background: #eeff2d;
}
.logo_bars > span:nth-child(2),
.logo_bars > span:nth-child(4)
{
	height: 23px;
	background: #2ac275;
}
.logo_bars > span:nth-child(3)
{
	height: 30px;
	background: #e830fe;
}
.logo_text
{
	line-height: 0.75;
	font-size: 30px;
	font-weight: 600;
	color: #ffffff;
	margin-left: 3px;
	transform: translateY(4px);
}
.main_nav
{
	transform: translateY(5px);
}
.main_nav ul li
{
	position: relative;
}
.main_nav ul li:not(:last-of-type)
{
	margin-right: 65px;
}
.main_nav ul li a
{
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}
.main_nav ul li a:hover,
.main_nav ul li.active a
{
	color: #2ac275;
}
.main_nav ul li.active::after
{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: -20px;
	width: 14px;
	height: 14px;
	background: #2ac275;
	border-radius: 50%;
	content: '';
	transition: all 400ms ease;
}
.header.scrolled .main_nav ul li.active::after
{
	bottom: -10px;
	width: 10px;
	height: 10px;
}
.log_reg
{
	position: absolute;
	top: 44%;
	right: 0;
	flex: 0 0 auto;
}
.header.scrolled .log_reg
{
	top: 50%;
	transform: translateY(-50%);
}
.log_reg ul
{
	display: block;
}
.log_reg ul li
{
	position: relative;
}
.log_reg ul li:not(:last-of-type)
{
	padding-right: 18px;
	margin-right: 1px;
}
.log_reg ul li:not(:last-of-type)::after
{
	position: absolute;
	top: 0;
	right: 0;
	content: '/';
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
	margin-left: 5px;
	margin-right: 5px;
}
.log_reg ul li a
{
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}
.log_reg ul li a:hover
{
	color: #2e9e7a;
}
.hamburger
{
	display: none !important;
	margin-left: 25px;
	width: 34px;
	height: 16px;
	padding: 2px;
	cursor: pointer;
	flex: 0 0 auto;
}
.hamburger > div
{
	background: #ffffff;
	height: 2px;
	transition: all 300ms ease;
}
.hamburger > div:first-child
{
	width: 100%;
}
.hamburger > div:nth-child(2)
{
	width: 50%;
}
.hamburger > div:nth-child(3)
{
	width: 75%;
}
.hamburger:hover > div
{
	width: 100%;
}
/*.hamburger:hover > div:first-child
{
	background: #e830fe;
}
.hamburger:hover > div:nth-child(2)
{
	background: #2ac275;
}
.hamburger:hover > div:nth-child(3)
{
	background: #eeff2d;
}*/

/*********************************
4. Menu
*********************************/

.menu
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 101;
	visibility: hidden;
	opacity: 0;
	transition: all 400ms ease;
}
.menu.active
{
	visibility: visible;
	opacity: 1;
}
.menu_container
{
	width: 100%;
	height: 100%;
}
.menu_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	opacity: 0.95;
}
.menu_close
{
	position: absolute;
	top: 50px;
	right: 50px;
	cursor: pointer;
}
.menu_close i
{
	font-size: 22px;
	color: #ffffff;
}
.menu_content
{
	position: absolute;
	top: 40%;
	transform: translateY(-50%);
	left: 0;
	width: 100%;
}
.menu_nav ul li
{
	position: relative;
	display: inline-block;
	
}
.menu_nav ul li::after
{
	display: block;
	position: absolute;
	top: 53%;
	transform: translateY(-50%);
	left: 0;
	width: 0;
	height: 3px;
	background: #ffffff;
	content: '';
	z-index: 1;
	transition: all 400ms ease;
}
.menu_nav ul li:hover::after
{
	width: 100%;
}
.menu_nav ul li a
{
	font-size: 36px;
	font-weight: 400;
	color: #ffffff;
}

/*********************************
5. Home
*********************************/

.home
{
	height: 449px;
}
.home_container
{
	position: absolute;
	left: 0;
	top: 57.2%;
	width: 100%;
}
.home_title
{
	font-size: 68px;
	font-weight: 400;
	color: #ffffff;
	line-height: 1.2;
}

/*********************************
6. Search
*********************************/

.search
{
	background: #ffffff;
}
.search_form_container
{
	padding-left: 119px;
	padding-right: 120px;
	margin-top: -65px;
}
.search_form
{
	display: block;
	height: 131px;
	border-radius: 66px;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
}
.search_form > div
{
	width: 100%;
	height: 100%;
	padding-left: 33px;
	padding-right: 38px;
}
.search_input
{
