.prlx
{
	height: 130% !important;
}
.parallax-window
{
    min-height: 400px;
    background: transparent;
}
.parallax_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.background_image
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}
.nopadding
{
	padding: 0px !important;
}
.owl-carousel,
.owl-carousel .owl-stage-outer,
.owl-carousel .owl-stage,
.owl-carousel .owl-item
{
	height: 100%;
}
.slide
{
	height: 100%;
}
.container_reset > .container
{
	max-width: 100%;
	padding-left: 0;
	padding-right: 0;
}
.container_reset > .container .row
{
	margin-left: 0;
	margin-right: 0;
}
.container_reset > .container .row > div[class^='col']
{
	padding-left: 0;
	padding-right: 0;
}
.button
{
	position: relative;
	overflow: hidden;
	width: 200px;
    height: 56px;
    border-radius: 22px;
    border: none;
    outline: none;
    cursor: pointer;
    text-align: center;
}
.button::after
{
	display: block;
	position: absolute;
	top: 0;
	left: -100%;
	width: 200%;
	height: 100%;
	background-color: #2ac275;
	background-image: linear-gradient(to right, #2ac275, #cbdc0b, #2ac275);
	background: #2ac275;
	background: linear-gradient(to right, #2ac275, #cbdc0b, #2ac275);
	content: '';
	z-index: 0;
	transition: all 400ms ease;
}
.button:hover::after
{
	left: 0;
}
.button a
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    text-transform: uppercase;
    z-index: 1;
    line-height: 56px;
}
.button span
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    line-height: 56px;
    text-transform: uppercase;
	z-index: 1;
}
.section_title h1
{
	font-size: 60px;
	font-weight: 400;
}

/*********************************
3. Header
*********************************/

.header
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	z-index: 100;
}
/*Header gradient bottom border*/
.header::after
{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	content: '';
	background-color: #2ac275;
	background-image: linear-gradient(to right, #eeff2d, #2ac275);
	background: #2ac275;
	background: linear-gradient(to right, #eeff2d, #2ac275);
}
.header_content
{
	width: calc(100% + 30px);
	left: -20px;
	height: 134px;
	transition: all 400ms ease;
}
.header.scrolled .header_content
{
	height: 75px;
}
.logo
{
	position: absolute;
	left: 0;
	top: 46%;
	transform: translateY(-50%);
}
.logo_bars
{
	width: 41px;
	height: 30px;
}
.logo_bars > span
{
	width: 6px;
}
.logo_bars > span:first-of-type,
.logo_bars > span:last-of-type
{
	height: 17px;
	background: #eeff2d;
}
.logo_bars > span:nth-child(2),
.logo_bars > span:nth-child(4)
{
	height: 23px;
	background: #2ac275;
}
.logo_bars > span:nth-child(3)
{
	height: 30px;
	background: #e830fe;
}
.logo_text
{
	line-height: 0.75;
	font-size: 30px;
	font-weight: 600;
	color: #ffffff;
	margin-left: 3px;
	transform: translateY(4px);
}
.main_nav
{
	transform: translateY(5px);
}
.main_nav ul li
{
	position: relative;
}
.main_nav ul li:not(:last-of-type)
{
	margin-right: 65px;
}
.main_nav ul li a
{
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}
.main_nav ul li a:hover,
.main_nav ul li.active a
{
	color: #2ac275;
}
.main_nav ul li.active::after
{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: -20px;
	width: 14px;
	height: 14px;
	background: #2ac275;
	border-radius: 50%;
	content: '';
	transition: all 400ms ease;
}
.header.scrolled .main_nav ul li.active::after
{
	bottom: -10px;
	width: 10px;
	height: 10px;
}
.log_reg
{
	position: absolute;
	top: 44%;
	right: 0;
	flex: 0 0 auto;
}
.header.scrolled .log_reg
{
	top: 50%;
	transform: translateY(-50%);
}
.log_reg ul
{
	display: block;
}
.log_reg ul li
{
	position: relative;
}
.log_reg ul li:not(:last-of-type)
{
	padding-right: 18px;
	margin-right: 1px;
}
.log_reg ul li:not(:last-of-type)::after
{
	position: absolute;
	top: 0;
	right: 0;
	content: '/';
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
	margin-left: 5px;
	margin-right: 5px;
}
.log_reg ul li a
{
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}
.log_reg ul li a:hover
{
	color: #2e9e7a;
}
.hamburger
{
	display: none !important;
	margin-left: 25px;
	width: 34px;
	height: 16px;
	padding: 2px;
	cursor: pointer;
	flex: 0 0 auto;
}
.hamburger > div
{
	background: #ffffff;
	height: 2px;
	transition: all 300ms ease;
}
.hamburger > div:first-child
{
	width: 100%;
}
.hamburger > div:nth-child(2)
{
	width: 50%;
}
.hamburger > div:nth-child(3)
{
	width: 75%;
}
.hamburger:hover > div
{
	width: 100%;
}
/*.hamburger:hover > div:first-child
{
	background: #e830fe;
}
