{
	background: #2ac275;
}
.hamburger:hover > div:nth-child(3)
{
	background: #eeff2d;
}*/

/*********************************
4. Menu
*********************************/

.menu
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 101;
	visibility: hidden;
	opacity: 0;
	transition: all 400ms ease;
}
.menu.active
{
	visibility: visible;
	opacity: 1;
}
.menu_container
{
	width: 100%;
	height: 100%;
}
.menu_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	opacity: 0.95;
}
.menu_close
{
	position: absolute;
	top: 50px;
	right: 50px;
	cursor: pointer;
}
.menu_close i
{
	font-size: 22px;
	color: #ffffff;
}
.menu_content
{
	position: absolute;
	top: 40%;
	transform: translateY(-50%);
	left: 0;
	width: 100%;
}
.menu_nav ul li
{
	position: relative;
	display: inline-block;
	
}
.menu_nav ul li::after
{
	display: block;
	position: absolute;
	top: 53%;
	transform: translateY(-50%);
	left: 0;
	width: 0;
	height: 3px;
	background: #ffffff;
	content: '';
	z-index: 1;
	transition: all 400ms ease;
}
.menu_nav ul li:hover::after
{
	width: 100%;
}
.menu_nav ul li a
{
	font-size: 36px;
	font-weight: 400;
	color: #ffffff;
}

/*********************************
5. Home
*********************************/

.home
{
	height: 449px;
}
.home_container
{
	position: absolute;
	left: 0;
	top: 57.2%;
	width: 100%;
}
.home_title
{
	font-size: 68px;
	font-weight: 400;
	color: #ffffff;
	line-height: 1.2;
}

/*********************************
6. Blog
*********************************/

.blog
{
	background: #ffffff;
	padding-top: 120px;
	padding-bottom: 120px;
}
.blog_post:not(:last-child)
{
	margin-bottom: 61px;
}
.blog_post_date
{
	display: inline-block;
	width: 113px;
	height: 30px;
	background: #2ac275;
	border-radius: 15px;
	text-align: center;
}
.blog_post_date a
{
	display: block;
	height: 100%;
	font-size: 16px;
	font-weight: 400;
	color: #ffffff;
	line-height: 34px;
}
.blog_post_title
{
	margin-top: 17px;
}
.blog_post_title a
{
	font-size: 30px;
	font-weight: 400;
	color: #2a2a2a;
	line-height: 1.2;
}
.blog_post_title a:hover
{
	color: #2ac275;
}
.blog_post_info
{
	margin-top: 0px;
}
.blog_post_info > ul > li:not(:last-of-type)::after
{
	display: inline-block;
	content: '|';
	margin-left: 8px;
	margin-right: 8px;
	font-size: 14px;
	font-weight: 600;
	color: #2a2a2a;
}
.blog_post_info > ul > li > ul > li:not(:last-of-type)::after
{
	display: inline-block;
	content: ',';
	margin-right: 4px;
	font-size: 14px;
	font-weight: 600;
	color: #2a2a2a;
}
.blog_post_info ul li,
.blog_post_info ul li a
{
	font-size: 14px;
	font-weight: 600;
	color: #2a2a2a;
}
.blog_post_info ul li a:hover
{
	color: #2ac275;
}
.blog_post_text
{
	margin-top: 21px;
}
.blog_post_link
{
	margin-top: 24px;
}
.blog_post_link a
{
	font-size: 14px;
	font-weight: 400;
	color: #2ac275;
	text-transform: uppercase;
}
.page_nav
{
	margin-top: 120px;
}
.page_nav ul li
{
	width: 27px;
	height: 27px;
	border-radius: 50%;
	background: transparent;
	text-align: center;
	transition: all 200ms ease;
}
.page_nav ul li:not(:last-of-type)
{
	margin-right: 3px;
}
.page_nav ul li.active,
.page_nav ul li:hover
{
	background: #2ac275;
}
.page_nav ul li a
{
	display: block;
	height: 100%;
	font-size: 16px;
	font-weight: 400;
	color: #515151;
	line-height: 29px;
}
.page_nav ul li.active a,
.page_nav ul li:hover a
{
	color: #ffffff;
}

/*********************************
7. Featured News
*********************************/

.featured_news
{
	padding-top: 42px;
	padding-bottom: 42px;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
}
.featured_news_image
{
	height: 100%;
	min-height: 450px;
}
.featured_news_content
{
	height: 100%;
	padding-right: 180px;
	padding-top: 66px;
	padding-left: 110px;
	padding-bottom: 95px;
}
.featured_news_title
{
	font-size: 72px;
	font-weight: 400;
	color: #ffffff;
	line-height: 1.2;
}
.featured_news .blog_post_date
{
	margin-top: 30px;
}
.featured_blog_post_title
{
	margin-top: 22px;
}
.featured_blog_post_title a
{
	font-size: 24px;
	font-weight: 700;
	color: #ffffff;
	line-height: 1.2;
}
.featured_blog_post_title a:hover
{
	color: #2ac275;
}
.featured_blog_post_text
{
	margin-top: 11px;
}
.featured_blog_post_text p
{
	color: #f0f0f0;
}

/*********************************
8. Newsletter
*********************************/

.newsletter
{
	background: #ffffff;
	padding-top: 117px;
	padding-bottom: 68px;
}
.newsletter_text
{
	margin-top: -12px;
	padding-left: 2px;
}
.newsletter_form_container
{
	padding-left: 40px;
	height: 100%;
	padding-bottom: 25px;
}
.newsletter_form
{
	display: block;
	position: relative;
	width: 100%;
}
.newsletter_input
{
	width: calc(100% - 215px);
	height: 56px;
	border-radius: 22px;
	border: solid 2px #e0e7ee;
	padding-left: 34px;
	outline: none;
	font-size: 16px;
	font-weight: 600;
	color: #000000;
}
.newsletter_button
{
	width: 208px;
	height: 56px;
	border-radius: 22px;
    cursor: pointer;
}
.newsletter_button span
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    line-height: 56px;
    text-transform: uppercase;
	z-index: 1;
}
.newsletter_input::-webkit-input-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input:-moz-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input::-moz-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
} 
.newsletter_input:-ms-input-placeholder
{ 
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input::input-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}

/*********************************
9. Footer
*********************************/

.footer
{
	display: block;
	position: relative;
}
.footer_content
{
	padding-top: 76px;
	padding-bottom: 74px;
}
.footer .logo
{
	display: inline-block;
	position: relative;
	left: auto;
	top: auto;
	transform: none;
}
.footer_social
{
	margin-top: 58px;
}
.footer_social ul li:not(:last-of-type)
{
	margin-right: 18px;
}
.footer_social ul li
{
	width: 46px;
	height: 46px;
	border-radius: 50%;
	background: #2ac275;
	transition: all 200ms ease;
}
.footer_social ul li:hover
{
	background: #eeff2d;
}
.footer_social ul li a
{
	display: block;
	height: 100%;
}
.footer_social ul li a i
{
	font-size: 20px;
	color: #ffffff;
	line-height: 46px;
	transition: all 200ms ease;
}
.footer_social ul li:hover a i
{
	color: #2ac275;
}
.copyright
{
	height: 49px;
	line-height: 49px;
}
