/*********************************
3. Header
*********************************/

.header
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	z-index: 100;
}
/*Header gradient bottom border*/
.header::after
{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	content: '';
	background-color: #2ac275;
	background-image: linear-gradient(to right, #eeff2d, #2ac275);
	background: #2ac275;
	background: linear-gradient(to right, #eeff2d, #2ac275);
}
.header_content
{
	width: calc(100% + 30px);
	left: -20px;
	height: 134px;
	transition: all 400ms ease;
}
.header.scrolled .header_content
{
	height: 75px;
}
.logo
{
	position: absolute;
	left: 0;
	top: 46%;
	transform: translateY(-50%);
}
.logo_bars
{
	width: 41px;
	height: 30px;
}
.logo_bars > span
{
	width: 6px;
}
.logo_bars > span:first-of-type,
.logo_bars > span:last-of-type
{
	height: 17px;
	background: #eeff2d;
}
.logo_bars > span:nth-child(2),
.logo_bars > span:nth-child(4)
{
	height: 23px;
	background: #2ac275;
}
.logo_bars > span:nth-child(3)
{
	height: 30px;
	background: #e830fe;
}
.logo_text
{
	line-height: 0.75;
	font-size: 30px;
	font-weight: 600;
	color: #ffffff;
	margin-left: 3px;
	transform: translateY(4px);
}
.main_nav
{
	transform: translateY(5px);
}
.main_nav ul li
{
	position: relative;
}
.main_nav ul li:not(:last-of-type)
{
	margin-right: 65px;
}
.main_nav ul li a
{
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}
.main_nav ul li a:hover,
.main_nav ul li.active a
{
	color: #2ac275;
}
.main_nav ul li.active::after
{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: -20px;
	width: 14px;
	height: 14px;
	background: #2ac275;
	border-radius: 50%;
	content: '';
	transition: all 400ms ease;
}
.header.scrolled .main_nav ul li.active::after
{
	bottom: -10px;
	width: 10px;
	height: 10px;
}
.log_reg
{
	position: absolute;
	top: 44%;
	right: 0;
	flex: 0 0 auto;
}
