{
	top: 50%;
	transform: translateY(-50%);
}
.log_reg ul
{
	display: block;
}
.log_reg ul li
{
	position: relative;
}
.log_reg ul li:not(:last-of-type)
{
	padding-right: 18px;
	margin-right: 1px;
}
.log_reg ul li:not(:last-of-type)::after
{
	position: absolute;
