/*********************************
4. Menu
*********************************/

.menu
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 101;
	visibility: hidden;
	opacity: 0;
	transition: all 400ms ease;
}
.menu.active
{
	visibility: visible;
	opacity: 1;
}
.menu_container
{
	width: 100%;
	height: 100%;
}
.menu_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
	opacity: 0.95;
}
.menu_close
{
	position: absolute;
	top: 50px;
	right: 50px;
	cursor: pointer;
}
.menu_close i
{
	font-size: 22px;
	color: #ffffff;
}
.menu_content
{
	position: absolute;
	top: 40%;
	transform: translateY(-50%);
	left: 0;
	width: 100%;
}
.menu_nav ul li
{
	position: relative;
	display: inline-block;
	
}
.menu_nav ul li::after
{
	display: block;
	position: absolute;
	top: 53%;
	transform: translateY(-50%);
	left: 0;
	width: 0;
	height: 3px;
	background: #ffffff;
	content: '';
	z-index: 1;
	transition: all 400ms ease;
}
.menu_nav ul li:hover::after
{
	width: 100%;
}
.menu_nav ul li a
{
	font-size: 36px;
	font-weight: 400;
	color: #ffffff;
}

