	opacity: 0;
}
.scroll_arrows > img:last-of-type
{
	-webkit-animation-delay: .15s;
	animation-delay: .15s;
}
@-webkit-keyframes arrowAnimation
{
	0%
	{
		opacity: 0;
	}
	50%
	{
		opacity: 1;
	}
	100%
	{
		opacity: 0;
	}
}
@keyframes arrowAnimation
{
	0%
	{
		opacity: 0;
	}
	50%
	{
		opacity: 1;
	}
	100%
	{
		opacity: 0;
	}
}

