	color: #6d6d6d;
}
.music_name a:hover
{
	color: #2e9e7a;
}
.music_button
{
	margin-top: 17px;
}
.musics_link
{
	margin-top: 14px;
	padding-left: 5px;
}
.musics_link a
{
	position: relative;
	font-size: 18px;
	font-weight: 400;
	color: #2ac275;
}
.musics_link a::after
{
	display: block;
	position: absolute;
	bottom: 2px;
	left: 0;
	width: 100%;
	height: 1px;
	background: #2ac275;
	content: '';
	transition: all 200ms ease;
}
.musics_link a:hover
{
	color: #cbdc0b;
}
.musics_link a:hover::after
{
	background: #cbdc0b;
}
.extra
{
	min-height: 700px;
	height: calc(100% - 12px);
	margin-top: 7px;
	padding-left: 25px;
	padding-right: 25px;
	padding-top: 55px;
	padding-bottom: 106px;
	text-align: center;
}
.extra_frame
{
	position: absolute;
	top: 22px;
	left: 26px;
	width: calc(100% - 53px);
	height: calc(100% - 46px);
	border: solid 2px #eeff2d;
	opacity: 0.59;
}
.extra_text
{
	font-size: 12px;
	font-weight: 700;
	color: #ffffff;
	line-height: 0.75;
	text-transform: uppercase;
}
.extra_year
{
	font-size: 92px;
	font-weight: 600;
	color: #2ac275;
	line-height: 0.75;
}
.extra_title
{
	font-size: 72px;
	font-weight: 700;
	color: #ffffff;
	text-transform: uppercase;
	line-height: 1;
	margin-top: 4px;
}
.extra_subtitle
{
	font-size: 18px;
	font-weight: 700;
	color: #ffffff;
	line-height: 0.75;
	text-transform: uppercase;
	margin-top: 8px;
}

/*********************************
8. App
*********************************/

.app
{
	padding-top: 123px;
	background: #eef2f6;
	padding-bottom: 117px;
}
.app_image
{
	width: calc(100% + 33px);
	transform: translateX(-67px);
}
.app_content
{
	height: 100%;
	transform: translateX(35px);
}
.app_text
{
	padding-left: 4px;
}
.stores
{
	margin-top: 68px;
	padding-left: 4px;
}
.stores > div:not(:last-of-type)
{
	margin-right: 19px;
}

/*********************************
9. Featured
*********************************/

.featured
{
	background: #ffffff;
	padding-top: 122px;
	padding-bottom: 89px;
}
.featured_row
{
	margin-top: 75px;
}
.featured_list
{
	width: calc(100% + 30px);
	left: -15px;
	margin-top: 82px;
}
.featured_list ul li
{
	position: relative;
	width: calc(100% / 3);
	min-height: 100px;
	padding-left: 15px;
	padding-right: 15px;
	margin-bottom: 30px;
}
.featured_list ul li > div:first-of-type
{
	position: absolute;
	left: 15px;
	bottom: 0;
	width: calc(100% - 30px);
	height: 78px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	background: rgba(0,0,0,0.66);
	z-index: 1;
	padding-left: 90px;
}
.featured_list .jp-artist
{
	display: none;
}
.featured_list .jp-playlist > ul > li > div:first-of-type a
{
	display: none;
	order: 2;
}
.featured_title
{
	font-size: 14px;
	font-weight: 400;
	color: rgba(255,255,255,0.53);
	outline: none;
}
.featured_list .jp-playlist > ul > li > div:first-of-type a:hover
{
	color: #2e9e7a;
}
.featured_album
{
	font-size: 18px;
	font-weight: 400;
	color: #ffffff;
	line-height: 1.2;
}
.album_play_button
{
	position: absolute;
	left: 35px;
	bottom: 16px;
	width: 45px;
	height: 45px;
	border-radius: 50%;
	background: #ff1a4b;
	z-index: 1;
	border: none;
	outline: none !important;
	cursor: pointer;
}
.album_play_button::after
{
	display: block;
    position: absolute;
    top: 50%;
    left: calc(50% + 3px);
    transform: translate(-50%, -50%);
    width: 20px;
    height: 23px;
    /* content: url(../images/play.png); */
}
.album_play_button.is-playing::after
{
	left: 50%;
	top: calc(50% - 1px);
	width: 14px;
    height: 13px;
	/* content: url(../images/stop.png); */
}

/*********************************
10. News
*********************************/

.news
{
	padding-top: 116px;
	padding-bottom: 124px;
	background-color: #3b125d;
	background-image: linear-gradient(to right, #2457d7, #3b125d);
	background: #3b125d;
	background: linear-gradient(to right, #2457d7, #3b125d);
}
.news .section_title
{
	text-align: center;
}
.news .section_title h1
{
	color: #ffffff;
}
.news_row
{
	margin-top: 68px;
}
.news_post.image_top .news_post_content
{
	padding-top: 41px;
	padding-bottom: 47px;
	padding-right: 15px;
}
.news_post.image_bottom .news_post_content
{
	padding-top: 42px;
	padding-bottom: 40px;
	padding-right: 15px;
}
.news_post_date
{
	width: 113px;
	height: 30px;
	border-radius: 15px;
	background: #2ac275;
	text-align: center;
}
.news_post_date a
{
	display: block;
	height: 100%;
	font-size: 16px;
	font-weight: 400;
	color: #ffffff;
	line-height: 30px;
}
.news_post_title
{
	margin-top: 18px;
}
.news_post_title span
{
	font-size: 24px;
	font-weight: 700;
	color: #ffffff;
}
.news_post_title span:hover
{
	color: #2ac275;
}
.news_post_text
{
	margin-top: 9px;
}
.news_post_text p
{
	color: #ffffff;
}

/*********************************
11. Newsletter
*********************************/

.newsletter
{
	background: #ffffff;
	padding-top: 77px;
	padding-bottom: 67px;
}
.newsletter_text
{
	margin-top: -12px;
	padding-left: 2px;
}
.newsletter_form_container
{
	padding-left: 40px;
	height: 100%;
	padding-bottom: 25px;
}
.newsletter_form
{
	display: block;
	position: relative;
	width: 100%;
}
.newsletter_input
{
	width: calc(100% - 215px);
	height: 56px;
	border-radius: 22px;
	border: solid 2px #e0e7ee;
	padding-left: 34px;
	outline: none;
	font-size: 16px;
	font-weight: 600;
	color: #000000;
}
.newsletter_button
{
	width: 208px;
	height: 56px;
	border-radius: 22px;
    cursor: pointer;
}
.newsletter_button span
{
	position: relative;
	font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    line-height: 56px;
    text-transform: uppercase;
	z-index: 1;
}
.newsletter_input::-webkit-input-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input:-moz-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input::-moz-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
} 
.newsletter_input:-ms-input-placeholder
{ 
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}
.newsletter_input::input-placeholder
{
	font-size: 16px !important;
	font-weight: 600 !important;
	color: #6d6d6d !important;
}

/*********************************
12. Footer
*********************************/

.footer
{
	display: block;
	position: relative;
}
.footer_content
{
	padding-top: 76px;
	padding-bottom: 74px;
}
.footer .logo
{
	display: inline-block;
	position: relative;
	left: auto;
	top: auto;
	transform: none;
}
.footer_social
{
	margin-top: 58px;
}
.footer_social ul li:not(:last-of-type)
{
	margin-right: 18px;
}
.footer_social ul li
{
	width: 46px;
	height: 46px;
	border-radius: 50%;
	background: #2ac275;
	transition: all 200ms ease;
}
.footer_social ul li:hover
{
	background: #eeff2d;
}
.footer_social ul li a
{
	display: block;
	height: 100%;
}
.footer_social ul li a i
{
	font-size: 20px;
	color: #ffffff;
	line-height: 46px;
	transition: all 200ms ease;
}
.footer_social ul li:hover a i
{
	color: #2ac275;
}
.copyright
{
	height: 49px;
	line-height: 49px;
}
